import React, { FunctionComponent } from "react";
import { lazy } from "react";
import { withTranslation, WithTranslation } from "react-i18next";

const Contact = lazy(() => import("../../components/ContactForm"));
const MiddleBlock = lazy(() => import("../../components/MiddleBlock"));
const Container = lazy(() => import("../../common/Container"));
const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
const ContentBlock = lazy(() => import("../../components/ContentBlock"));

const Home: FunctionComponent<WithTranslation> = ({ t }) => {
  return (
    <Container>
      <ScrollToTop />
      <ContentBlock
        type="right"
        title={t("introTitle")}
        content={t("introText")}
        button={[
          { title: t("startCollecting") },
          { title: t("learnMore"), color: "#fff" }
        ]}
        icon={`./img/svg/developer.svg`}
        id="intro"
      />
      <MiddleBlock
        title={t("middleBlockTitle")}
        content={t("middleBlockText")}
        button={t("exploreCollection")}
      />
      <ContentBlock
        type="left"
        title={t("aboutContentTitle")}
        content={t("aboutContentText")}
        section={[
          {
            title: t("whySnakesTitle"),
            content: t("whySnakesContent"),
            icon: "./img/svg/snake-icon.svg"
          },
          {
            title: t("communityDrivenTitle"),
            content: t("communityDrivenContent"),
            icon: "./img/svg/community-icon.svg"
          }
        ]}
        icon={`./img/svg/graphs.svg`}
        id="about"
      />
      <ContentBlock
        type="right"
        title={t("missionTitle")}
        content={t("missionText")}
        icon={`./img/svg/product-launch.svg`}
        id="mission"
      />
      <ContentBlock
        type="left"
        title={t("getInvolvedTitle")}
        content={t("getInvolvedText")}
        icon={`./img/svg/waving.svg`}
        id="product"
      />
      <Contact
        title={t("getInTouchTitle")}
        content={t("getInTouchText")}
        id="contact"
      />
    </Container>
  );
};

export default withTranslation()(Home);
